@import url(https://fonts.googleapis.com/css?family=Titillium+Web&display=swap);
* {
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
  background: rgb(247, 99, 31);
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(247, 99, 31, 0.8802871490393032)), to(rgba(255, 0, 31, 1)));
  background: linear-gradient(180deg, rgba(247, 99, 31, 0.8802871490393032) 0%, rgba(255, 0, 31, 1) 100%);
  display: grid;
  grid-template-columns: 1fr;
  align-items: center;
  height: 100%;
  width: 100%;
  font-family: 'Titillium Web';
}
.App {
  text-align: center;
}

.TimeDisplay {
    position: relative;
}

.TimeDisplay svg {
    max-width: 300px;
}

.TimeDisplay circle {
    -webkit-transition: stroke-dashoffset 1s;
    transition: stroke-dashoffset 1s;
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
}

.TimeDisplay>div {
    position: absolute;
    left: 50%;
    top: 50%;
    display: grid;
    align-content: center;
    -webkit-transform: translate(-50%, -32px);
            transform: translate(-50%, -32px);
    text-align: center;
}

.TimeDisplay h1 {
    font-size: 52px;
    font-weight: 300;
    color: #D9534F;
    letter-spacing: 2px;
    margin: 0;
}

.TimeDisplay p {
    font-size: 14px;
    text-transform: uppercase;
    color: #bbb;
    letter-spacing: 3px;
    margin: 10px 0 0;
}
